ه شد. که با این تغییرات، این امکان برای کارگردان فراهم آمد تا تصاویر ویدئویی کاندیداها به شکل موزائیکی در یک صفحه نمایش داده شده و نیز زمان استفاده شده توسط هر کدام به صورت Real Time  به ذیل آن افزوده شود."}}),_c('app-paragragh',{attrs:{"text":"همچنین در سال 96 اندازه گیری و محاسبه زمان به شکل سخت‌افزاری صورت می‌گرفت تا بیشترین دقت و کمترین میزان خطا را برای سامانه فراهم نماید. بر اساس مطالعات نیاز سنجی و برآورد اولیه، دقت محاسبات آن هزارم ثانیه تعیین شد و این سامانه به شکلی طراحی گردید که بتواند زمان گفتگو برای 32 نفر را محاسبه نموده و نمایش دهد. طراحی آن نیز به نحوی صورت گرفت که صدای افراد مجاور روی زمان سنج تاثیری نداشته باشد."}}),_c('app-paragragh',{attrs:{"text":"نکته چالش برانگیز دیگر تشخیص و اعمال آستانه زمانی \"سکوت طبیعی بین کلام\" و یا \"پایان گفتگو\" بود که تمهیدات لازم برای آن در نظر گرفته شد. "}}),_c('app-paragragh',{attrs:{"text":"در «سامانه هوشمند محاسبه زمان» ابتدا توسط نرم¬افزار چیدمان کاندیداها ، مشخصات آنها و تایمر زمانی بر روی مانیتور اصلی طراحی شده و به دستگاه  MIP  (Multi Image)  که تولید و طراحی آن به طور کامل توسط متخصصان گروه صمیم انجام شده است ارسال می¬شود. سیگنال‌های ارسالی از میکروفون‌ها به کارت هدایت شده و ضمن تحلیل و تجزیه سیگنال دریافتی، شمارنده هر کاندیدا در سطح سخت افزار فعال می¬شود."}}),_c('app-paragragh',{attrs:{"text":"این سامانه با دریافت صدای میکروفون‌ها از طریق مبادی ورودی صدای آنالوگ و دیجیتال، با تشخیص سطح بلندی صدا به طور لحظه‌ای (Peak to Peak) به صورت آنلاین قادر به شناسایی زمان سکوت و گفتگو است که میزان آن بر اساس dBFs (واحد سطح صدای دیجیتال) بین dbfs 72- و dbfs 24- قابل تنظیم است  (Silent Detection)."}}),_c('app-paragragh',{attrs:{"text":"در عین حال لختی هوشمند (Intelligent Latency) آن برای تشخیص آستانه زمانی سکوت طبیعی بین کلام و یا پایان گفت وگو (سخنرانی) نیز بین ۳۰۰ هزارم ثانیه تا ۵ ثانیه قابل تنظیم است. یعنی اگر سخنران بین کلام خود بسته به تنظیم صورت گرفته بین ۳۰۰ هزارم ثانیه تا ۵ ثانیه سکوت کند، سامانه آن را پایان سخنرانی قلمداد نکرده و زمان سنج این زمان را نیز به درستی محاسبه می کند."}}),_c('app-paragragh',{attrs:{"